









































































































































































































import Vue from 'vue';
import {mapMutations, mapState} from 'vuex';

function requestDefaultValues(index: number) {
    return {
        name: 'Unidad ' + index,
        subject: null,
        schoolLevel: null,
        level: null,
        course: null,
        year: new Date().getFullYear(),
        startDate: new Date().toISOString().substr(0, 10),
        endDate: new Date().toISOString().substr(0, 10),
    };
}

export default Vue.extend({
    name: 'RomaUnitModalForm',
    created() {
        // si va a editar
        if (this.unit) {
            this.request = this.unit;
        }
    },
    props: {
        value: Boolean,
        school: {
            type: Object,
            required: true,
        },
        maxWidth: {
            type: String,
            default: '800',
        },
        overlayOpacity: {
            type: String,
            default: '0.8',
        },
        persistent: {
            type: Boolean,
            default: true,
        },
    },
    data() {
        return {
            request: requestDefaultValues((this.school.units.length + 1)),
            valid: false,
            loading: false,
            showing: this.value,
            startDateMenu: false,
            endDateMenu: false,
            courses: ['A', 'B', 'C', 'D', 'E', 'F'],
        };
    },
    computed: {
      ...mapState([
          'roma',
      ]),
    },
    methods: {
        show(value) {
            this.$emit('input', value);
            this.request = requestDefaultValues((this.school.units.length + 1));
        },
        ...mapMutations({
            setUnit(commit) {
                const data = {
                    unit: this.request,
                    school: this.school,
                };
                commit('setUnit', data);
                this.show(false);
                this.request = requestDefaultValues((this.school.units.length + 1));
            },
        }),
    },
});
